import { useRef, useState } from "react";
import { useEffect } from "react";
import DataTable from "../Components/DataTable";
import Modal from "../Components/Modal";
import Thumbnail from "../Components/Thumbnail";
import fetch from "../Services/fetch";
import { teamRoles, CONSTANT } from "../Services/String";
const $ = window.$;
const XLSX = window.XLSX;
const moment = window.moment;

const ContractorEvalSummary = () => {
    const tblRef = useRef();
    const header = [
        {title: 'First Name', data: 'contactPerson'},
        {title: 'Middle Name', data: 'contactPerson'},
        {title: 'Last Name', data: 'contactPerson'},
        {title: 'Email', data: 'contactPerson'},
      ];
    
    const[selected, setSelected] = useState({
        team:[],
        documents: {
            DO174: null,
            BZNPERMIT: null,
            COR: null,
            SEC: null,
            PCAB: null,
            BIO: null
        }
    });
    const[rows, setRows] = useState([]);

    useEffect(()=>{
        
        getContractors();
        $('#staticBackdrop').on('hidden.bs.modal',(e) => {
        //    setIsUpdate(false); 
        });
        
        //Date range picker
        $('#reservation').daterangepicker({
            opens: 'right'
        }, (start, end, label) => {
            getContractors({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 22:59:59')});
        });

    }, []);


    // const[isUpdate, setIsUpdate] = useState(false);
    const getSelected = (rowData) =>{
        // setIsUpdate(true);
        setSelected(rowData);
        $('#staticBackdrop').modal('show');
    }
    
    const getContractors = (dateRange) =>{
        let showMsg = true;
        if(!dateRange ) {
            dateRange = {from: moment().format('YYYY-MM-DD 00:00:00') , end: moment().format('YYYY-MM-DD 23:59:59')};
            showMsg = false;
        }
        fetch.get(`${process.env.REACT_APP_HOST}/api/contractor-evalation/${dateRange.from}/${dateRange.end}`)
        .then(res=>{
            tblRef.current.init(res);
            setRows(res);
            showMsg && window.toastr.success(CONSTANT.SUCCESS);
        }).catch(er=>{
            showMsg && window.toastr.error(CONSTANT.ERRROR);
        });
    }

     // render the team form fields
     const renderTeamFields = (obj, teamIndex, personIndex) => {
        switch(obj.type){
          
            default:
              return (
                <div className="form-group col-sm-12 col-lg-4 col-md-6" key={personIndex}>
                  <label htmlFor="exampleInputPassword1">{teamRoles(obj.role)}</label>
                    <input type="text" 
                    defaultValue={obj.value} name={obj.role}
                    className="form-control" placeholder="" />
                </div>
              );
        }
  
      }

    // render team form
    const renderTeam = () => {
        return selected.team.map((team, teamIndex) => {
        return (<div id={"team-container"+teamIndex} className="row col-12 legend-container" key={teamIndex}>
                <div className="col-12 pb-1 mb-4 font-weight-bold legend-group">
                    {team.title}
                </div>
                <div className="col-lg-12 clearfix"></div>
                {team.person.map((person, personIndex)=>{
                return renderTeamFields(person, teamIndex, personIndex);
                })}
        </div>);
        });
      }

    const downloadXls = () => {
      let wb = XLSX.utils.book_new();

            let data = [];
            let header = [
                'CONTRACTOR NAME','REMARKS'
            ];
            data.push(header);
            
            rows.forEach(obj => {
                // let records = [];
                // obj.entries.forEach((entry, i)=>{
                  console.log(obj);
                        let records = [
                            obj.contactPerson,
                            obj.contactPerson, 
                        ];
                    
                    data.push(records);
                // });
                // records.push();

            });
          

            // /* SHEET 1 */
            // let ws = XLSX.utils.aoa_to_sheet([["a","b", "c"],[3,2,1],[1,2,3]]);
            let ws = XLSX.utils.aoa_to_sheet(data);
            XLSX.utils.book_append_sheet(wb, ws, "Sheet1");

            XLSX.writeFile(wb, "ContractorEvalSummary"+".xls");
            /* uncomment writeFile to start saving excel */
    }
    return (<>

        {/* Date range */}
        <div className="row">
            <div className="col-md-4 col-sm-6 col-12">
                <div className="info-box bg-primary px-3">
                    {/* <!-- /.form group --> */}
                    {/* <!-- Date range --> */}
                    <div className="form-group w-100">
                        <label>Date range:</label>

                        <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                            <i className="far fa-calendar-alt"></i>
                            </span>
                        </div>
                        <input type="text" className="form-control float-right" id="reservation" />
                        </div>
                        {/* <!-- /.input group --> */}
                    </div>
                    {/* <!-- /.form group --> */}
                </div>
                {/* <!-- /.info-box --> */}
            </div>
        </div>

        {/* card */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Contractor Evaluation Summary Table</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <div id="custom-tbl-btn col-12">
                  <button className="btn btn-primary" onClick={downloadXls}>
                      Download XLS
                  </button>
                </div>
                <DataTable 
                getSelected={getSelected}
                header={header}
                ref={tblRef}
                // buttons={true}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
        {/* <!-- /.card --> */}

        {/* <!-- Modal --> */}
        <Modal title="Contractor Information"> 
            <div className="card-body row">
                    {/* <button onClick={testUpdate}>Test update</button> */}
                    <div className="card card-primary card-outline card-tabs w-100">
                        <div className="card-header p-0 pt-1 border-bottom-0">
                            <ul className="nav nav-tabs" id="custom-tabs-three-tab" role="tablist">
                            <li className="nav-item">
                                <a className="nav-link active" id="custom-tabs-three-form-tab" data-toggle="pill" href="#custom-tabs-three-form" role="tab" aria-controls="custom-tabs-three-form" aria-selected="false">Form</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-team-tab" data-toggle="pill" href="#custom-tabs-three-team" role="tab" aria-controls="custom-tabs-three-team" aria-selected="false">Team</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-docu-tab" data-toggle="pill" href="#custom-tabs-three-docu" role="tab" aria-controls="custom-tabs-three-docu" aria-selected="true">Documents</a>
                            </li>
                            </ul>
                        </div>
                        <div className="card-body">
                            <div className="tab-content" style={{overflow: "hidden"}}>
                            <div className="tab-pane fade active show" id="custom-tabs-three-form" role="tabpanel" aria-labelledby="custom-tabs-three-form-tab">
                                    <div className="mt-3 row">
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputEmail1">Name of Contractor</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                // defaultValue={Auth.userInfo.firstName}
                                                disabled
                                                name="contractorName"
                                                type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Location</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                // value={form.location}
                                                name="location"
                                                type="text" className="form-control" placeholder="Location" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Region</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                // value={form.region}
                                                name="region"
                                                type="text" className="form-control" placeholder="Region" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Email</label>
                                                <input 
                                                // defaultValue={Auth.userInfo.email}
                                                disabled
                                                type="text" className="form-control" placeholder="Email" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Contact Person</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                // value={form.contactPerson}
                                                name="contactPerson"
                                                type="text" className="form-control" placeholder="Contact Person" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Contact Number</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                // value={form.contactNum2}
                                                name="contaactNum2"
                                                type="text" className="form-control" placeholder="Contact Number" />
                                            </div>  
                                    </div>
                            </div>
                            <div className="tab-pane fade" id="custom-tabs-three-team" role="tabpanel" aria-labelledby="custom-tabs-three-profile-tab">
                                <div className="mt-3 row">
                                    {renderTeam()}
                                </div>
                            </div>
                            <div className="tab-pane fade" id="custom-tabs-three-docu" role="tabpanel" aria-labelledby="custom-tabs-three-messages-tab">
                                <div className="mt-3 row">
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'DO174',
                                            title: 'DOLE DO174',
                                            src: selected.documents.DO174,
                                            }
                                        }/>
                                        
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'BZNPERMIT',
                                            title: 'Business Permit',
                                            src: selected.documents.BZNPERMIT,
                                            }
                                        }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'COR',
                                            title: 'Certificate of Registration',
                                            src: selected.documents.COR,
                                            }
                                        }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'SEC',
                                            title: 'SEC Registration',
                                            src: selected.documents.SEC,
                                            }
                                        }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'PCAB',
                                            title: 'PCAB Liscense',
                                            src: selected.documents.PCAB,
                                            }
                                        }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                        <Thumbnail props = {
                                            {
                                            name: 'BIO',
                                            title: 'Billing / Invoice / O.R',
                                            src: selected.documents.BIO,
                                            }
                                        }/>
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        
                    </div>                    
                    <div className="form-group w-100">
                        <label>Remarks</label>
                        <textarea className="form-control" rows="3" placeholder="Enter ..."></textarea>
                    </div>

            </div>
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                {/* {isUpdate && 
                (<><button type="button" className="btn btn-danger">Reject</button>
                <button type="button" className="btn btn-primary">Confirm</button></>)} */}
                
            </div>
        </Modal> 
                
    </>);
}
 
export default ContractorEvalSummary;