import React, {Component} from 'react';
import Auth from '../../Auth';
import './login.css';
import icdi from '../../Images/icdi_mini_logo.png';
import fetch from '../../Services/fetch';
import Validator from '../../Services/Validator';
import { CONSTANT } from '../../Services/String';
const toastr = window.toastr;
toastr.options = {
    "debug": false,
    "closeButton": true,
    "preventDuplicates": true,
    "newestOnTop": true,
    "fadeIn": 300,
    "fadeOut": 1000,
    "timeOut": 1000,
    // "extendedTimeOut": 1000
  }
class Login extends Component {

    constructor(props){
        super(props);
        this.history = props.history;
        this.location = props.location;
        this.state = {
            email: '',
            password: '',
            errMsg: {
                count: 0,
            },
        }
    }

    handleChange = (e) =>{
        // console.log(e.target.name, e.target.value);
        this.setState({[e.target.name]: e.target.value});
    }
    login = async (e) =>{
        e.preventDefault();
        
        // axios.post(process.env.REACT_APP_SERVE+'/login')
        // .then(res=>{
        try {
            const authuser = await fetch.post(`${process.env.REACT_APP_HOST}/api/login`,{
                email: this.state.email,
                password: this.state.password
            })
    
            if(authuser.status === 200) {
                const {from} = this.location.state || {from: {pathname: '/'}};
                Auth.login(authuser.data, ()=>{
                    this.history.push(from)
                });
            }else{
                toastr.error(authuser.data.message);
            }   
        } catch (error) {
            toastr.error(CONSTANT.ERRROR);
        }
        
            
        // })
        // .catch(er=>console.log(er))
        
    }
    register = (e) =>{
        e.preventDefault();
        this.history.push('/register');   
    }
    validate = (e) => {
        e.preventDefault();
        const target = e.target;
        const message = Validator([target.name], target.value);
        
        message ? this.setState({errMsg: {[target.name] : message, count: 1}})
        : this.setState({errMsg: {[target.name] : message, count: 0}});

    }
    render() { 
        if(Auth.isAuth) return this.history.goBack();
        return (
            <div className="container-fluid">
            <div className="row no-gutter">
                {/* <!-- The image half --> */}
                <div className="col-md-6 d-none d-md-flex bg-image"></div>
        
        
                {/* <!-- The content half --> */}
                <div className="col-md-6 bg-light">
                    <div className="login d-flex align-items-center">
        
                        {/* <!-- Demo content--> */}
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-9 col-xl-6 mx-auto clr-primary">
                                    <img src={icdi} alt="" style={{width: '100%', height: 'auto', }}/>
                                    <h3 className="display-4">4<sup>th</sup>JARB</h3>
                                    <p className="text-muted mb-4">Login.</p>
                                    <form>
                                        <div className="form-group mb-3">
                                            <input id="inputEmail" name="email" onChange={this.handleChange} value={this.state.email} onBlur={this.validate}
                                            type="text" placeholder="Email" required="" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            <small className="text-danger">{this.state.errMsg.email || ''}</small>
                                        </div>
                                        <div className="form-group mb-3">
                                            <input id="inputPassword" name="password" onChange={this.handleChange} value={this.state.password}
                                            type="password" placeholder="Password" required="" className="form-control rounded-pill border-0 shadow-sm px-4 py-4 text-primary" />
                                        </div>
                                        
                                        {/* <!-- <div className="custom-control custom-checkbox mb-3">
                                            <input id="customCheck1" type="checkbox" checked className="custom-control-input">
                                            <label for="customCheck1" className="custom-control-label">Remember password</label>
                                        </div> --> */}
                                        <div className="mt-4">
                                            <button type="submit" onClick={this.login} 
                                            className="btn btn-primary btn-block text-uppercase mb-2 rounded-pill shadow-sm py-3">Sign in</button>
                                        </div>
                                        <p className="col-12 pt-2 text-center">
                                            <span className="font-weight-bold text-secondary">Click here to</span> <a href="/#"onClick={this.register} >Register</a>
                                        </p>
                                        {/* <!-- <div className="text-center d-flex justify-content-between mt-4"><p>Snippet by <a href="https://bootstrapious.com/snippets" className="font-italic text-muted"> 
                                                <u>Boostrapious</u></a></p></div> --> */}
                                    </form>
                                </div>
                            </div>
                        </div>
                        {/* <!-- End --> */}
        
                    </div>
                </div>
                {/* <!-- End --> */}
        
            </div>
        </div>
        );
    }
}
 
export default Login;