import React from 'react';
const $ = window.$;
// const moment = window.moment;

class DataTable extends React.Component{
    _isMounted = false;
    constructor(props) {
        super(props);
        this.el = React.createRef();
        this.selectedRow = [];
        this.state = {
            data: [],
            header:[],
        };
    }
    init(data = [])
    {
        
        const header = []
        if(data.length) {
            for (const prop in data[0]) {
                header.push({
                    title: prop, data: prop
                });
            }
        }
        
        this.setState({
            data:  data,
        });
    }
    add(data) {
        const newData = this.state.data;
        newData.push(data);
        this.setState({...this.state, data: newData});
    }
    update(key, data = {})
    {
        let rows = this.state.data;   
        rows = rows.map(item => {
            if(item[key] === data[key]){
                item = {...item, ...data};
            }
            return item;
        });

        this.setState({
            data:  rows,
        });
    }
    
    remove(data){
        const [val] = Object.entries(data);
        const rows = this.state.data.filter((item) => item[val[0]] !== val[1])
        this.init(rows);
    }

    getSelected(row = null){   
        if(this.props.getSelected)
        this.props.getSelected(row);
    }
    componentDidUpdate(){
        
        this.$el.clear();
        this.$el.rows.add(this.state.data);
        this.$el.draw();
    }
    async componentDidMount(){
        this._isMounted = true;
        this.$el = $(this.el.current);

        
        this.$el = this.$el.DataTable({
            columns: this.props.header && this.props.header.length? this.props.header : [{ data: 'name',     title: 'Table Data' }],
            data: this.state.data,
            "responsive": true, "lengthChange": false, "autoWidth": false,
            // "buttons": ["copy", "csv", "excel", "pdf", "print", "colvis"]
            "buttons": this.props.buttons?["csv","print", "colvis"]:[]
        });
        this.$el.buttons().container().appendTo('#dataTableComponent_wrapper .col-md-6:eq(0)');

        const table = this.$el;
        const self = this;
        $('#dataTableComponent tbody').on( 'click', 'tr', function(){
            if(table.row(this).data())
            self.getSelected(table.row(this).data());
            
        } );
        // this.setState({data: [
        //     {
        //         "name":       "Tiger Nixon",
        //         "position":   "System Architect",
        //         "salary":     "$3,120",
        //         "start_date": "2011/04/25",
        //         "office":     "Edinburgh",
        //         "extn":       "5421"
        //     },
        //     {
        //         "name":       "Garrett Winters",
        //         "position":   "Director",
        //         "salary":     "$5,300",
        //         "start_date": "2011/07/25",
        //         "office":     "Edinburgh",
        //         "extn":       "8422"
        //     }
        // ]});
    }
    componentWillUnmount () {
        this._isMounted = false;    
    }
    render(){
        return(
            <table id="dataTableComponent" className="table table-bordered table-striped" ref={this.el}></table>
        );
    }
}

export default DataTable;