export const CONSTANT = {
  ERRROR: 'An error encountered please try again',
  SUCCESS: 'Success',
}

export const removeExtraSpaces = (str) => {
  return str.replace(/\s+/g,' ').trim();
}


export const teamRoles = (role) =>{
  const roles = {
    teamlead:         'Project Supervisor/ Team Leader',
    foreman:          'Foreman',
    siteEnginner:     'Site Engineer',
    safetyOfficer:    'Safety Officer',
    masonCarpenter:   'Mason/Carpenter',
    electrician:      'Electrician',
    rigger:           'Structural Erector/ Rigger',
    labor:            'Labor',

  };
  return role ? roles[role] : roles;
}
