import { useEffect, useRef, useState } from "react";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
import fetch from "../../Services/fetch";
import { CONSTANT } from "../../Services/String";
const $ = window.$;
const toastr = window.toastr;
const ManagePosition = () => {
  const tblRef = useRef();
  const[isUpdate, setIsUpdate] = useState(false);
  const[state, setState] = useState({
    position: '',
  });
  const header = [
    {title: 'Position Name', data: 'position'},
  ];
  useEffect(()=>{
    fetch.get(`${process.env.REACT_APP_HOST}/api/pos`)
    .then(res=>{
      tblRef.current.init(res.data);
    })
    .catch(er=>{
      console.log(er)
    });

    // modal
    $('#staticBackdrop').on('hidden.bs.modal', closeModal);
  },[]);
  
  const closeModal = () => {
    setState({position: ''});
    setIsUpdate(false); 
  }

  const getSelected = (rowData) => {
    setIsUpdate(true);
    setState(rowData);
    $('#staticBackdrop').modal('show');
  }

  const handleChange = (e) => {
    setState({...state, [e.target.name]: e.target.value});
  }

  const addNew = async () => {
    fetch.post(`${process.env.REACT_APP_HOST}/api/pos/create`, state)
    .then(res=>{
      toastr.success(CONSTANT.SUCCESS);
      tblRef.current.add(res.data);
      $('#staticBackdrop').modal('hide');
    })
    .catch(er=>{
      toastr.error(CONSTANT.ERRROR);
      console.log(er)
    });
  }

  const update = () => {
    fetch.post(`${process.env.REACT_APP_HOST}/api/pos/${state._id}/update`, state)
    .then(res=>{
      toastr.success(CONSTANT.SUCCESS);
      tblRef.current.update('_id', state);
      $('#staticBackdrop').modal('hide');
    })
    .catch(er=>{
      toastr.error(CONSTANT.ERRROR);
      console.log(er)
    });
    
  }

  const remove = () => {
    fetch.get(`${process.env.REACT_APP_HOST}/api/pos/${state._id}/remove`)
    .then(res=>{
      toastr.success(CONSTANT.SUCCESS);
      tblRef.current.remove({_id: state._id});
      $('#staticBackdrop').modal('hide');
    })
    .catch(er=>{
      toastr.error(CONSTANT.ERRROR);
      console.log(er)
    });
    
  }
  return (
    <>
    <div className="card mt-3 card-outline card-primary">
        <div className="card-header">
          <h3 className="card-title">Position Lists</h3>
        </div>
        {/* <!-- /.card-header --> */}
        <div className="card-body">
            <button className="btn btn-primary" data-toggle="modal" data-target="#staticBackdrop">New Position</button>
            <DataTable 
              getSelected={getSelected}
              header={header}
              buttons={false}
              ref={tblRef}
            />
        </div>
        {/* <!-- /.card-body --> */}
    </div>
    <Modal title="Manage Position" size="modal-md">
        <div className="card-body row">
          <div className="col-12">
              <label htmlFor="exampleInputEmail1">Position Name</label>
              <input type="text" 
              onChange={handleChange} value={state.position}
              name="position"
              className="form-control" id="exampleInputEmail1" placeholder="Name" />
          </div>
        </div>
        <div className="modal-footer card-footer text-right">
            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
            { isUpdate?
                (<>
                <button type="button" 
                onClick={remove}
                className="btn btn-danger" >Delete</button>
                <button type="button" 
                onClick={update}
                className="btn btn-primary" >Update</button>
                </>) :
                (<button type="button" 
                onClick={addNew}
                className="btn btn-primary" >Save</button>)
            }
        </div>
    </Modal>
    </>
   );
}
 
export default ManagePosition;