
import { NavLink } from "react-router-dom";

const PageListComponent = ({page}) => {
  const pages = page || [];
  return (<>
  {pages.map( data => (
      <li className="nav-item" key={data._id} >
          <NavLink exact to={data.path} className="nav-link">
          <i className="far fa-circle nav-icon"></i>
          <p>{data.title}</p>
          </NavLink>
      </li>
  ))}
  </>);
}
 
export default PageListComponent;