/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import Auth from "../../Auth";
import Modal from "../../Components/Modal";
import UploadSites from "../../Components/UploadSites";
import fetch from '../../Services/fetch';
import { CONSTANT } from "../../Services/String";
const google = window.google;
const $ =  window.$;
const toastr = window.toastr;
const AllSites = () => {

  const [showSearch, setShowSearch] = useState(false);
  const [search, setSearch] = useState('');
  const [searchList, setSearchList] = useState([]);

  const [map, setMap] = useState('');
  const[locations, setLocations] = useState([]);
  const[site, setSite] = useState({
    location: '',
    address: '',
    siteContactPerson: '',
    siteContactNum: '',
    _idcontractor: '',
    contractor: '',
  });

  // udpate search list on changes of locations and search keywords
  useEffect(()=>{
    let count = 0;
    const searchResult = locations.filter(map => {
      return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
        && count++ < 10;
      // return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      // if(map.address && map.siteContactPerson){
      //   return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      //   && count++ < 10;
      // }
      // if(map.address){
      //   return (map.address.toLowerCase().includes(search))
      //   && count++ < 10;
      // }
      
      // return (map.siteContactPerson.toLowerCase().includes(search))
      // && count++ < 10;
    }
      // (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      // && count++ < 10
    );
    setSearchList(searchResult)
  }, [locations, search])
  
  // pull map records
  useEffect(()=>{
    fetch.get(`${process.env.REACT_APP_HOST}/api/site`)
    .then(res => {
      setLocations(res.data);
    })
    .catch(er=>{
      console.log(er);
      toastr.error('An error encountered, please try again')
    })
  },[]);

  // init map
  useEffect(() => {
    const map = new google.maps.Map(document.getElementById("map"), {
      center: { lat: 10.714222484489797, lng: 123.92758604369578 },
      zoom: 6,
    });
    setMap(map);
    
    let marker;
    const infowindowContainer = []; 
    for (let i = 0; i < locations.length; i++) {  
      addMarker(map, marker, locations[i], infowindowContainer);
    }
  }, [locations]);

  const addMarker = (map, marker, location, infowindowContainer) => {
    
    // read only button
    const readOnly = Auth.role !== 'standard';

    // info window
    //  let infowindow = new google.maps.InfoWindow(); 
     
      
      let coor = location.location.split(',');
      //  console.log(coor);
       marker = new google.maps.Marker({
         position: new google.maps.LatLng(Number(coor[0]), Number(coor[1])),
         map: map
       });
      // const icon = {

      //   path: "M 150.94 192 h 33.73 c 11.01 0 18.61 -10.83 14.86 -21.18 c -4.93 -13.58 -7.55 -27.98 -7.55 -42.82 s 2.62 -29.24 7.55 -42.82 C 203.29 74.83 195.68 64 184.67 64 h -33.73 c -7.01 0 -13.46 4.49 -15.41 11.23 C 130.64 92.21 128 109.88 128 128 c 0 18.12 2.64 35.79 7.54 52.76 c 1.94 6.74 8.39 11.24 15.4 11.24 Z M 89.92 23.34 C 95.56 12.72 87.97 0 75.96 0 H 40.63 c -6.27 0 -12.14 3.59 -14.74 9.31 C 9.4 45.54 0 85.65 0 128 c 0 24.75 3.12 68.33 26.69 118.86 c 2.62 5.63 8.42 9.14 14.61 9.14 h 34.84 c 12.02 0 19.61 -12.74 13.95 -23.37 c -49.78 -93.32 -16.71 -178.15 -0.17 -209.29 Z M 614.06 9.29 C 611.46 3.58 605.6 0 599.33 0 h -35.42 c -11.98 0 -19.66 12.66 -14.02 23.25 c 18.27 34.29 48.42 119.42 0.28 209.23 c -5.72 10.68 1.8 23.52 13.91 23.52 h 35.23 c 6.27 0 12.13 -3.58 14.73 -9.29 C 630.57 210.48 640 170.36 640 128 s -9.42 -82.48 -25.94 -118.71 Z M 489.06 64 h -33.73 c -11.01 0 -18.61 10.83 -14.86 21.18 c 4.93 13.58 7.55 27.98 7.55 42.82 s -2.62 29.24 -7.55 42.82 c -3.76 10.35 3.85 21.18 14.86 21.18 h 33.73 c 7.02 0 13.46 -4.49 15.41 -11.24 c 4.9 -16.97 7.53 -34.64 7.53 -52.76 c 0 -18.12 -2.64 -35.79 -7.54 -52.76 c -1.94 -6.75 -8.39 -11.24 -15.4 -11.24 Z m -116.3 100.12 c 7.05 -10.29 11.2 -22.71 11.2 -36.12 c 0 -35.35 -28.63 -64 -63.96 -64 c -35.32 0 -63.96 28.65 -63.96 64 c 0 13.41 4.15 25.83 11.2 36.12 l -130.5 313.41 c -3.4 8.15 0.46 17.52 8.61 20.92 l 29.51 12.31 c 8.15 3.4 17.52 -0.46 20.91 -8.61 L 244.96 384 h 150.07 l 49.2 118.15 c 3.4 8.16 12.76 12.01 20.91 8.61 l 29.51 -12.31 c 8.15 -3.4 12 -12.77 8.61 -20.92 l -130.5 -313.41 Z M 271.62 320 L 320 203.81 L 368.38 320 h -96.76 Z",
      //   // fillColor: '#FF0000',
      //   fillOpacity: .6,
      //   anchor: new google.maps.Point(0,0),
      //   strokeWeight: 0,
      //   scale: 1,
      //   // width: 4,
      //   // height: 4,
      // }

      // marker = new google.maps.Marker({
      //   position: new google.maps.LatLng(Number(coor[0]), Number(coor[1])),
      //   icon: icon,
      //   map: map,
        // label: {
        //   fontFamily: "'Font Awesome 5 Free'",
        //   text: '\uf0f9', //icon code
        //   fontWeight: '900', //careful! some icons in FA5 only exist for specific font weights
        //   // color: '#FFFFFF', //color of the text inside marker
        // },
        // labelContent: '<i class="fa fa-send fa-3x" style="color:rgba(153,102,102,0.8);"></i>',
        // labelAnchor: new google.maps.Point(22, 50),
        // labelClass: "labels" // the CSS class for the label
      // });

       const contentString = `
            <form id="infowindowMap">
             <div class="container">
               <div class="row mt-3">
               <div class="form-group col-12">
                     <label htmlFor="exampleInputEmail1">Site Address</label>
                     <input 
                     value="${location.address}"
                     ${readOnly && 'readOnly'}
                     name="address"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Contact Person</label>
                     <input 
                     value="${location.siteContactPerson}"
                     ${readOnly && 'readOnly'}
                     name="siteContactPerson"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Contact Number</label>
                     <input 
                     value="${location.siteContactNum}"
                     ${readOnly && 'readOnly'}
                     name="siteContactNum"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Coordinates</label>
                     <input 
                     value="${coor.join(', ')}"
                     ${readOnly && 'readOnly'}
                     name="location"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Team Coordinator</label>
                     <input 
                     defaultValue={Auth.userInfo.firstName}
                     ${readOnly && 'readOnly'}
                     name="contractorName"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
               </div>
               <div class="${ readOnly ? "d-none": "form-group col-12 text-right"}">
               <button class="btn btn-danger" id="btn0">Delete</button>   
               <button class="btn btn-primary" id="btn1">Update</button>
               </div>
             </div>
           </form>`;

      //  const self = this;
      
       marker.addListener('click', function() {
          // if (typeof infowindow != 'undefined') infowindow.close();
          
          let infowindow = new google.maps.InfoWindow({
            content: contentString,
          })
          infowindowContainer.push(infowindow);
          for (const iterator of infowindowContainer) {
              iterator.close();
          }
          infowindow.open(map, marker);

          google.maps.event.addListener(infowindow, 'domready', function() {
              //  update
              document.getElementById("btn1").addEventListener("click", async (e)=>{
                e.preventDefault();
                const form = document.querySelector('#infowindowMap')
                const entries = Object.fromEntries(new FormData(form).entries());
                const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/${location._id}/update`, entries);
                if(res.status === 200){
                  toastr.success(CONSTANT.SUCCESS);
                  const locIdex  = locations.findIndex(loc=>loc._id === location._id);
                  locations[locIdex] = {...locations[locIdex], ...entries};
                  setLocations([]);
                  setLocations(locations);
                  infowindow.close();
                }else{
                  toastr.error(CONSTANT.ERRROR);
                }
              });

              //  delete
              document.getElementById("btn0").addEventListener("click", async (e)=>{
                e.preventDefault();
                const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/${location._id}/remove`);
                if(res.status === 200){
                  const tempLocation = locations.filter(loc => loc.location !== location.location);
                  setLocations(tempLocation);
                  toastr.success(CONSTANT.SUCCESS); 
                } else{
                  toastr.error(CONSTANT.ERRROR); 
                }
              });
          });

          // change zoom settings
          map.setCenter(marker.getPosition());
          map.setZoom(10);
       });
  }

  const newMarker = async () =>{
    const marker = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/create`,
      {
        location: site.location,
        address: site.address,
        siteContactPerson: site.siteContactPerson,
        siteContactNum: site.siteContactNum,
        _idcontractor: site._idcontractor
      });

    setLocations([...locations, marker.data]);
    $('#staticBackdrop').modal('hide');
  }

  const handleChange = (e) => {
    const target =  e.target;
    if(target.name === 'location')
    target.value = (target.value).replace(/ /g, "");
    setSite({...site, [target.name] : target.value });
  }

  // set focus show search sidebar and focus on clicked search result
  const setFocus = (location = null) => {
    setShowSearch(true);
    if(location){
      location = location.split(", ")
      map.setCenter({ lat: Number(location[0]),  lng: Number(location[1])});
      map.setZoom(15);
    }
  }

  // handle seaerch
  const handleSearch = (e) => {
    // const keyword = (e.target.value).replace(/ /g, "");
    const keyword = (e.target.value).trim();
    setSearch(keyword);
  }

  const uploaded = (val)=>{
    setLocations(val);
  }

  return (
  <div style={{
    position: 'relative'
  }}>
    {/* add marker */}
    <button className="btn btn-white"
      style={{
        position:'absolute',
        zIndex: 1,
        top:50,
        right: 0,
        margin: 10
      }}
      data-toggle="modal" data-target="#staticBackdrop"
      ><i className="fas fa-map-marker-alt" style={{fontSize: '1.1em', color: 'red'}}></i>
    </button>

    {/* search */}
    <button className="btn btn-white d-nonex" onClick={()=>setFocus()}
    style={{
      position:'absolute',
      zIndex: 1,
      top:100,
      right: 0,
      margin: 10
    }}
    >
      <i className="fas fa-search" style={{fontSize: '.9em'}}></i>
    </button>
    
    {/* upload to cloud */}
    <div
      style={{
        position: 'absolute',
        zIndex: 1,
        top: 150,
        right: 0,
        margin: 10
      }}
    >
        <UploadSites uploaded={uploaded}/>
    </div>
      
      {/* map */}
      <div id="map" style={
        {
          width: '100%',
          height: '80vh',
          position: 'relative'
        }
      }>
      </div>
    {/* search right sidebar */}
    <div
    id="fadeInRight"
    style={{
      position: "fixed",
      width: '240px',
      height: '80%',
      right: 22,
      top: 87,
      zIndex: 1,
      background: 'rgba(255,255,255,1)',
      overflow: "hidden",
    }}
    className={showSearch ? "row active": "row"}
    >
      <div className="container-fluid py-4 mt-3">
        <div className="close" style={{position: 'absolute', top: 10, right: 10, cursor: 'pointer'}}>
          <span aria-hidden="true" onClick={()=>setShowSearch(false)}>×</span>
        </div>
        <div className="input-group col-12">
            {/* <label htmlFor="exampleInputEmail1">First Name</label> */}
            <div className="input-group-prepend">
              <span className="input-group-text bg-primary">
              <i className="fas fa-search" style={{fontSize: '.9em'}}></i>
              </span>
            </div>
            <input type="text" 
            onChange={handleSearch} 
            name="firstName"
            className="form-control" id="exampleInputEmail1" placeholder="search" />
        </div>
        {/* list group */}
        <div className="col-12">
          <div className="list-group-flush mt-2">
            {searchList.map((map, key)=>
              (<button type="button" className="list-group-item list-group-item-action px-0" key={key} onClick={()=>setFocus(map.location)}>
                {map.address}
              </button>)
            )}
          </div>
        </div>
        {/* end group*/}
      </div>
    </div>

    {/* modal */}
    <Modal title="Add New Site" size="modal-md">
      <div className="card-body row">
        <div className="form-group col-sm-12 col-lg-12 col-md-12">
            <label htmlFor="exampleInputEmail1">Site Address</label>
            <input 
            onChange={handleChange}
            value={site.address}
            name="address"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Contact Person</label>
            <input 
            onChange={handleChange}
            value={site.siteContactPerson}
            name="siteContactPerson"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Contact Number</label>
            <input 
            onChange={handleChange}
            value={site.siteContactNum}
            name="siteContactNum"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Coordinates</label>
            <input 
            onChange={handleChange}
            value={site.location}
            name="location"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Contractor</label>
            <input 
            onChange={handleChange}
            value={site.contractor}
            name="contractor"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
      </div>
      <div className="modal-footer card-footer text-right">
          <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
          
          <button type="button" className="btn btn-primary" onClick={newMarker} >Add</button>
      </div>
    </Modal>
  </div>);
  
}
 
export default AllSites;