import { useEffect } from "react";
import fetch from "../Services/fetch";

const XLSX = window.XLSX;
const toastr = window.toastr;

const UploadSites = ({uploaded}) => {
  useEffect(()=> {

  }, [])
  const handleChange = (e) => {
    let files = e.target.files,file;
    if (!files || files.length == 0) return;
    file = files[0];
    let fileReader = new FileReader();
    fileReader.onload = async (e) =>{
        
        let binary = "";
        let bytes = new Uint8Array(e.target.result);
        let length = bytes.byteLength;
        for (let i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
        }
        let workbook = XLSX.read(binary, {type: 'binary', cellDates:true, cellStyles:true});

        //console.log(oFile);
        let first_sheet_name = workbook.SheetNames[0];
        let worksheet = workbook.Sheets[first_sheet_name];
        let xlsData = await XLSX.utils.sheet_to_json(worksheet);

        // xlsData = await xlsData.reduce((acc, cur, i, arr) => {
        //     acc.push({
        //       location: cur['Coordinates'],
        //       address: `${cur['Barangays']} ${cur['Barangays_1']} ${cur['Municipality']}`,
        //       siteContactPerson:  cur['Owner/s'],
        //       _idcontractor: cur['Team'],
        //     });
        //     return acc;
        // }, []);
        let count = 0;
        let acc = [];
        for (const cur of xlsData) {

          if(count <= 200){
            acc.push({
              location: cur['Coordinates'] || '',
              address: `${cur['Barangays'] || ''} ${cur['Barangays_1'] || ''} ${cur['Municipality'] || ''}`,
              siteContactPerson:  cur['Owner/s'] || '',
              _idcontractor: cur['Team'] || '',
            });
            count ++;
          }
          if(count === 200){
            
            await fetch.post(`${process.env.REACT_APP_HOST}/api/site/upload`, acc)
            .then(res => {
                uploaded(res.data);
            })
            .catch(er=>{
              console.log(er);
              toastr.error('An error encountered, please try again')
            });
            count = 0;
            acc = [];
          }
          

        }

        // fetch.post(`${process.env.REACT_APP_HOST}/api/site/upload`, xlsData)
        // .then(res => {
        //     uploaded(res.data);
        //     console.log('success..', res.data);
        // })
        // .catch(er=>{
        //   console.log(er);
        //   toastr.error('An error encountered, please try again')
        // })

    };
    fileReader.readAsArrayBuffer(file);
  }
  return (<>
    <label className="btn btn-white" htmlFor="upload-site" >
      <i className="fas fa-cloud-upload-alt" style={{fontSize: '.7em'}}></i>
    </label>
    <input type="file" name="file" onChange={handleChange} className="d-none" id="upload-site"/>
  </>);
}
 
export default UploadSites;