import { useEffect, useState } from "react";
import Auth from "../../Auth";
import Modal from "../../Components/Modal";
import Thumbnail from "../../Components/Thumbnail";
import fetch from "../../Services/fetch";
import { teamRoles, CONSTANT} from "../../Services/String";
import './style.css';
const $ = window.$;
const toastr = window.toastr;


const Documents = () => {
    
    const userInfo = Auth.userInfo;
    const[disableNewTeam, setDisableNewTeam] = useState(false);
    const[finalStep, setFinalStep] = useState(false);    
    const[err, setErr] = useState({});
    const[newRole, setNewRole] = useState({
        role: '',
        inputTye:'text',
        value: '',
    });

    const newTeam =
      {
        title: 'Team 1',
        person: [
          {
            role: 'teamlead',
            inputTye:'text',
            value: '',
          },
          {
            role: 'foreman',
            inputTye:'text',
            value: '',
          },
          {
            role: 'siteEnginner',
            inputTye:'text',
            value: '',
          },
          {
            role: 'safetyOfficer',
            inputTye:'text',
            value: '',
          },
          {
            role: 'masonCarpenter',
            inputTye:'text',
            value: '',
          },
          {
            role: 'masonCarpenter',
            inputTye:'text',
            value: '',
          },
          {
            role: 'electrician',
            inputTye:'text',
            value: '',
          },
          {
            role: 'rigger',
            inputTye:'text',
            value: '',
          },
          {
            role: 'rigger',
            inputTye:'text',
            value: '',
          },
          {
            role: 'rigger',
            inputTye:'text',
            value: '',
          },
          {
            role: 'labor',
            inputTye:'text',
            value: '',
          },
        ]
      };

    const[form, setForm] = useState({
      email: '',
      firstName: '',
      lastName: '',
      middleName: '',
      address: '',
      contactNum: '',
      civilStatus: '',
      contactPerson: '',
      contactNum2: '',
      bankNumber: '',
      bankName: '',
      bankBranch: '',
      region: '',
      team: (userInfo.team && userInfo.team.length > 0 ) ? userInfo.team : [newTeam],
      documents: userInfo.documents || {}
    });
    
    // watch for changes
    useEffect(()=>{
      // show user dropdown
      $('.fa-user-plus').on('click', function(e){
          e.preventDefault();
          $(this).parent().next().children('.dropdown').show('fast')
      });
      // hide user dropdown
      $('.hide-fa-user-plus-dropdown').on('click', function(e){
          e.preventDefault();
          $(this).closest('.dropdown').hide('linear');
      });
      
      $('.toastrDefaultError').click(function() {
        window.toastr.error('Lorem ipsum dolor sit amet, consetetur sadipscing elitr.')
      });
      
      // animate new added group
      $("#team-container0").addClass('active');
      setTimeout(()=>{ 
        $("#team-container0").removeClass('active');
        setDisableNewTeam(false);
      }, 1000);
      
    },[form.team]);
    
    const formStep = (e) =>{
        return e.target.id === 'custom-tabs-three-docu-tab'? setFinalStep(true): setFinalStep(false);
    }
    
    // add new team group
    const appendNewTeam = () => {
      setDisableNewTeam(true)
      let title = form.team.length ? (form.team[0]['title'].split(" ")[1]) : 0;
      title = parseInt(title) + 1;
      setForm(()=>{
       return {...form, team:[{...newTeam, title: `Team ${title}`}, ...form.team]}
      });
    }
    const handleChangeNewRole = (e) => {
      setNewRole({...newRole, [e.target.name]: e.target.value});
    } 

    // add new role to the team
    const appendNewRole = (index, e) => {
        e.preventDefault();
        const selectedTeam = form.team[index];
        selectedTeam.person.push(newRole);
        
        const newTeamGroup = form.team;
        newTeamGroup[index] = selectedTeam;

        setForm({...form, team: newTeamGroup});
        setNewRole({role: '', inputTye:'text', value: '',});
        $('.add-staff.dropdown').hide('linear');
    }

    // render the team form fields
    const renderTeamFields = (obj, teamIndex, personIndex) => {
      switch(obj.type){
        
          default:
            return (
              <div className="form-group col-sm-12 col-lg-4 col-md-6" key={personIndex}>
                <label htmlFor="exampleInputPassword1">{teamRoles(obj.role)}</label>
                  <input type="text" 
                  value={obj.value} onChange={(e)=>{
                    handleBlur(e);
                    handleChangeTeam(e, teamIndex, personIndex)
                  }} name={obj.role}
                  
                  className="form-control" placeholder="" />
              </div>
            );
      }

    }
    const handleChangeTeam = (e, teamIndex, personIndex) => {
        const target = e.target;
        const selectedTeam = form.team[teamIndex];


      


        // point to selcted person
        selectedTeam.person[personIndex]['value'] = target.value;
        
        // const newTeam = form.team;
        // newTeam[teamIndex]['person'][personIndex]['value'] = e.target.value;
        setForm({...form});

    }

    const handleBlur = (e) => {
      e.preventDefault();
      const target = (e.target.value).replace(/ /g, '');
   
      // check for duplicate names
      const duplicate = checkDuplicateTeam(e.target.value);
      // console.log(duplicate);
      if(duplicate.length > 0 && target) {
      //   toastr.error('Duplicate name');
        setErr({ ...err, [target]: true });
      }else {
        delete err[target];
        setErr(err);
      }
      
    }

    // check duplicate names
    const checkDuplicateTeam = (val) => {
      
       return form.team.reduce((acc, curr)=>{
            acc.push(...curr.person);
            return acc;
        },[]).filter( rec => rec.value === val);
        
        // filter(data => data.value === val);
    }

    const removeGroup = (teamIndex) =>{
      const selectedTeam = form.team;
      selectedTeam.splice(teamIndex, 1);
      setForm({...form});
    }
   
    // render team form
    const renderTeam = () => {
      return form.team.map((team, teamIndex) => {
      return (<div id={"team-container"+teamIndex} className="row col-12 legend-container" key={teamIndex}>
              <div className="col-12 pb-1 mb-4 mt-5 font-weight-bold legend-group">
                          {team.title}
                          <i onClick={()=>removeGroup(teamIndex)}
                            className={form.team.length > 1 ? "fas fa-trash-restore-alt text-danger ml-3" : "d-none"}>
                          </i>
                          <i className="fas fa-user-plus text-info ml-3"></i>
                        </div>
                        <div className="relative">
                          <div className="px-3 pt-3 add-staff dropdown">
                            <button type="button" className="hide-fa-user-plus-dropdown close">
                              <span aria-hidden="true">&times;</span>
                            </button>
                            <div className="row">
                              <div className="form-group col-sm-12 col-lg-5 col-md-6">
                                <label>Select Position</label>
                                <select onChange={handleChangeNewRole} value={newRole.role} name="role" className="form-control">
                                  <option value="">Please Select</option>
                                  <option value="teamlead">Project Supervisor/ Team Leader</option>
                                  <option value="foreman">Foreman</option>
                                  <option value="siteEnginner">Site Engineer</option>
                                  <option value="safetyOfficer">Safety Officer</option>
                                  <option value="masonCarpenter">Mason/Carpenter</option>
                                  <option value="electrician">Electrician</option>
                                  <option value="rigger">Structural Erector/ Rigger</option>
                                  <option value="labor">Labor</option>
                                </select>
                              </div>
                              <div className="form-group col-sm-12 col-lg-6 col-md-6">
                                  <label htmlFor="exampleInputPassword1">Name</label>
                                  <input type="text" onChange={handleChangeNewRole} value={newRole.value} name="value"
                                  className="form-control" placeholder="Name" />
                              </div>
                              <div className="form-group col-sm-12 col-lg-1 col-md-6 d-flex">
                                <button type="button" className="btn btn-primary align-bottom align-self-end" 
                                disabled={!newRole.value}
                                onClick={(e)=>appendNewRole(teamIndex, e, form.title)}>
                                  Save
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                        
                        
                        <div className="col-lg-12 clearfix"></div>
                        {team.person.map((person, personIndex)=>{
                          return renderTeamFields(person, teamIndex, personIndex);
                        })}
      </div>);
      });
    }
    // const testUpdate = async () =>{
    //   const user = await fetch.post(`${process.env.REACT_APP_HOST}/api/user/${Auth._id}/update`);
    //   console.log(user);
    // }
    const handleChangeInfo = (e) =>{
      setForm({...form, [e.target.name] : e.target.value});
    }
    const save = () => {
          
        fetch.post(`${process.env.REACT_APP_HOST}/api/contractor/${Auth._id}/update`, form)
        .then(res=>{
          Auth.userInfo = {...Auth.userInfo, ...form};
          localStorage.setItem('user', JSON.stringify(Auth.userInfo));
          toastr.success(CONSTANT.SUCCESS);
        })
        .catch(er=>{
          console.log(er)
          toastr.error(CONSTANT.ERRROR);
        })
    };
    return (
        <>
        {/* <button onClick={testUpdate}>Test update</button> */}
        <div className="card card-primary card-outline card-tabs">
              <div className="card-header p-0 pt-1 border-bottom-0">
                <ul className="nav nav-tabs" id="custom-tabs-three-tab" role="tablist">
                  <li className="nav-item">
                    <a className="nav-link active" onClick={formStep} id="custom-tabs-three-form-tab" data-toggle="pill" href="#custom-tabs-three-form" role="tab" aria-controls="custom-tabs-three-form" aria-selected="false">Form</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" onClick={formStep} id="custom-tabs-three-team-tab" data-toggle="pill" href="#custom-tabs-three-team" role="tab" aria-controls="custom-tabs-three-team" aria-selected="false">Team</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" onClick={formStep} id="custom-tabs-three-docu-tab" data-toggle="pill" href="#custom-tabs-three-docu" role="tab" aria-controls="custom-tabs-three-docu" aria-selected="true">Documents</a>
                  </li>
                </ul>
              </div>
              <div className="card-body">
                <div className="tab-content" style={{overflow: "hidden"}}>
                  <div className="tab-pane fade active show" id="custom-tabs-three-form" role="tabpanel" aria-labelledby="custom-tabs-three-form-tab">
                        <div className="mt-3 row">
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputEmail1">Name of Contractor</label>
                                    <input 
                                    // onChange={handleChangeInfo}
                                    defaultValue={Auth.userInfo.firstName}
                                    disabled
                                    name="contractorName"
                                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                                </div>
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputPassword1">Location</label>
                                    <input 
                                    onChange={handleChangeInfo}
                                    value={form.location}
                                    name="location"
                                    type="text" className="form-control" placeholder="Location" />
                                </div>
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputPassword1">Region</label>
                                    <input 
                                    onChange={handleChangeInfo}
                                    value={form.region}
                                    name="region"
                                    type="text" className="form-control" placeholder="Region" />
                                </div>
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputPassword1">Email</label>
                                    <input 
                                    defaultValue={Auth.userInfo.email}
                                    disabled
                                    type="text" className="form-control" placeholder="Email" />
                                </div>
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputPassword1">Contact Person</label>
                                    <input 
                                    onChange={handleChangeInfo}
                                    value={form.contactPerson}
                                    name="contactPerson"
                                    type="text" className="form-control" placeholder="Contact Person" />
                                </div>
                                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                    <label htmlFor="exampleInputPassword1">Contact Number</label>
                                    <input 
                                    onChange={handleChangeInfo}
                                    value={form.contactNum2}
                                    name="contaactNum2"
                                    type="text" className="form-control" placeholder="Contact Number" />
                                </div>  
                        </div>
                  </div>
                  <div className="tab-pane fade" id="custom-tabs-three-team" role="tabpanel" aria-labelledby="custom-tabs-three-profile-tab">
                     <div className="mt-3 row">
                        <div className="col-12 mb-3 text-right">
                            <button type="button" className="btn btn-primary" onClick={appendNewTeam} disabled={disableNewTeam}>
                              New Team
                            </button>
                        </div>
                        
                        {renderTeam()}
                     </div>
                  </div>
                  <div className="tab-pane fade" id="custom-tabs-three-docu" role="tabpanel" aria-labelledby="custom-tabs-three-messages-tab">
                     <div className="mt-3 row">
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'DO174',
                                  title: 'DOLE DO174',
                                  src: form.documents.DO174,
                                }
                              }/>
                        </div>
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'BZNPERMIT',
                                  title: 'Business Permit',
                                  src: form.documents.BZNPERMIT,
                                }
                              }/>
                        </div>
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'COR',
                                  title: 'Certificate of Registration',
                                  src: form.documents.COR,
                                }
                              }/>
                        </div>
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'SEC',
                                  title: 'SEC Registration',
                                  src: form.documents.SEC,
                                }
                              }/>
                        </div>
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'PCAB',
                                  title: 'PCAB Liscense',
                                  src: form.documents.PCAB,
                                }
                              }/>
                        </div>
                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                              <Thumbnail props = {
                                {
                                  name: 'BIO',
                                  title: 'Billing / Invoice / O.R',
                                  src: form.documents.BIO,
                                }
                              }/>
                        </div>
                     </div>
                  </div>
                </div>
              </div>
              {/* card footer */}
              <div className="card-footer text-right">
                {/* <button type="button" onClick={nextStep} className={!finalStep? "btn btn-primary": "d-none"} >Next</button> */}
                <button type="button" onClick={save} className={finalStep? "btn btn-primary": "d-none"} >Finish</button>
              </div>
              {/* /.card footer */}
              {/* <!-- /.card --> */}
            </div>

            {/* MODAL */}
            <Modal title="Add More" size="modal-xl">
              <div className="card-body row">
                  {/* {newTeamModal()} */}
              </div>
              <div className="modal-footer card-footer text-right">
              <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" className="btn btn-primary" onClick={appendNewTeam}>Add</button>
              </div>
            </Modal>
        
     </>);
}
 
export default Documents;