
import { isEmpty } from 'lodash';
import { useState } from 'react';
import Auth from '../../Auth';
import fetch from '../../Services/fetch';
import Validator from '../../Services/Validator';
// import '../Login/login.css';
// import icdi from '../../icdi_mini_logo.png';

const Register = ({history}) => {
    const[data, setData] = useState({
        email: '',
        firstName: '',
        lastName: '',
        middleName: '',
        address: '',
        contactNum: '',
        role: 'standard',
    });

        
    const [err, setErr] =  useState({});
    const handleChange = (e) =>{
        const value = e.target.name==='role'? e.target.checked: e.target.value;    
        setData({...data, [e.target.name]: value});
    }
    const login = (e) => {
        e.preventDefault();
        history.push('/login');
    }
    const register = async (e) => {
        e.preventDefault();
        
        // uncomment to check if some values is empty
        // console.log(values(data).some(isEmpty), isEmpty(''));
        // return;
        
        try {
            const postData = data;
            postData.role = postData.role ? 'contractor': 'standard-user';
            const authuser = await fetch.post(`${process.env.REACT_APP_HOST}/api/register`, postData)
            
            if(authuser.status === 200){
                Auth.login(authuser.data, ()=>{
                    history.push('/');
                })
            }
            
        } catch (error) {
         console.log(error);   
        }
    }

    const validate = (e) => {
        e.preventDefault();
        const dataSet = e.target.name;
        const message = Validator(dataSet, e.target.value);
        
        // set error message
        if(message){
            setErr({...err, [e.target.name] : message});
        }
        else{
            delete err[e.target.name];
            setErr({...err});
        }
    }

    if(Auth.isAuth) return this.history.goBack();
    return (
        <div className="container-fluid">
            <div className="row no-gutter">
                {/* <!-- The image half --> */}
                <div className="col-md-6 d-none d-md-flex bg-image"></div>
        
        
                {/* <!-- The content half --> */}
                <div className="col-md-6 bg-light">
                    <div className="login d-flex align-items-center">
        
                        {/* <!-- Demo content--> */}
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-9 col-xl-6 mx-auto">
                                    <h3 className="display-4 text-primary">4<sup>th</sup>JARB</h3>
                                    <p className="text-muted mb-4">Register.</p>
                                    <form>
                                        <div className="form-group mb-3">
                                            <input type="text" name="email" onChange={handleChange} value={data.email} onBlur={validate}
                                            placeholder="Email" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            {err.email && <small className="text-danger ml-3">{err.email || ''}</small> }
                                        </div>
                                        <div className="form-group mb-3">
                                            <input type="text" name="firstName" onChange={handleChange} value={data.firstName} onBlur={validate}
                                            placeholder="First Name" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            {err.firstName && <small className="text-danger ml-3">{err.firstName || ''}</small> }
                                        </div>
                                        <div className="form-group mb-3">
                                            <input type="text" name="middleName" onChange={handleChange} value={data.middleName}
                                            placeholder="Middle Name" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                        </div>
                                        <div className="form-group mb-3">
                                            <input type="text" name="lastName" onChange={handleChange} value={data.lastName} onBlur={validate}
                                            placeholder="Last Name" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            {err.lastName && <small className="text-danger ml-3">{err.lastName || ''}</small> }
                                        </div>
                                        <div className="form-group mb-3">
                                            <input type="text" name="address" onChange={handleChange} value={data.address} onBlur={validate}
                                            placeholder="Address" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            {err.address && <small className="text-danger ml-3">{err.address || ''}</small> }
                                        </div>
                                        <div className="form-group mb-3">
                                            <input type="text" name="contactNum" onChange={handleChange} value={data.contactNum} onBlur={validate}
                                            placeholder="Contact #" className="form-control rounded-pill border-0 shadow-sm px-4 py-4" />
                                            {err.contactNum && <small className="text-danger ml-3">{err.contactNum || ''}</small> }
                                        </div>
                                        {/* <div className="col-12 form-group clearfix">
                                            <div className="icheck-primary d-inline text-primary">
                                                <input type="checkbox" name="role" onChange={handleChange} checked={data.role} onBlur={validate}
                                                id="checkboxPrimary3" disabled="" />
                                                <label htmlFor="checkboxPrimary3">
                                                    Are you a Contractor?
                                                </label>
                                            </div>
                                        </div> */}
                                        <div className="mt-4 text-center">
                                            <button type="submit" onClick={register} 
                                            disabled={!isEmpty(err) ? true: ""}
                                            className="btn btn-primary btn-block rounded-pill py-3">Register</button>
                                        </div>
                                        <p className="col-12 pt-3 text-center">
                                            <span className="font-weight-bold text-secondary">Click here to</span> <a href="/#"onClick={login} >Sign In</a>
                                        </p>
                                            
                                        
                                        {/* </div> */}
                                    </form>
                                </div>
                            </div>
                        </div>
                        {/* <!-- End --> */}
        
                    </div>
                </div>
                {/* <!-- End --> */}
        
            </div>
        </div>
    );
}
 
export default Register;