import {useEffect, useRef, useState } from "react";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
import fetch from "../../Services/fetch";
import { CONSTANT } from "../../Services/String";
const $ =  window.$;
const moment = window.moment;
const ManageUser = () => {
  const tblRef = useRef();
  const header = [
    {title: 'First Name', data: 'firstName'},
    {title: 'Middle Name', data: 'middleName'},
    {title: 'Last Name', data: 'lastName'},
    {title: 'Email', data: 'email'},
  ];

  const formData = {
    _id: '',
    firstName    : '',
    lastName     : '',
    middleName   : '',
    birthDate    : '',
    civilStatus  : '',
    contactNum   : '',
    address      : '',
    email        : '',
    position     : '',
    department   : '',
    branch       : '',
    dateHired    : '',
    SSS          : '',
    HDMF         : '',
    philHealth   : '',
    role         : '',
    primarySch   : '',
    secondarySch : '',
    tertiarySch  : '',
    course       : '',
    company1     : '',
    year1        : '',
    company2     : '',
    year2        : '',
    company3     : '',
    year3        : '',
  };
  
  const[isUpdate, setIsUpdate] = useState(false);
  const[selected, setSelected] = useState(formData);  

  const[dept, setDept] = useState([]);
  const[branch, setBranch] = useState([]);
  const[pos, setPos] = useState([]);

  useEffect(()=>{
    // get user data
    fetch.get(`${process.env.REACT_APP_HOST}/api/user`)
    .then(res=>{
      tblRef.current.init(res.data);
    })
    .catch(er=>{
      console.log(er)
    });

    // position
    fetch.get(`${process.env.REACT_APP_HOST}/api/pos`)
    .then(res=>{
      setPos(res.data);
    })
    .catch(er=>{
      console.log(er)
    });

    // dept
    fetch.get(`${process.env.REACT_APP_HOST}/api/dept`)
    .then(res=>{
        setDept(res.data);
    })
    .catch(er=>{
      console.log(er)
    });

    // branch
    fetch.get(`${process.env.REACT_APP_HOST}/api/branch`)
    .then(res=>{
        setBranch(res.data);
    })
    .catch(er=>{
      console.log(er)
    });

    // daterange picker single
    $('input[name="birthDate"]').daterangepicker({
        singleDatePicker: true,
        showDropdowns: true,
        minYear: 1980,
        // locale: {
        //     format: 'M/DD hh:mm A'
        // },
        autoUpdateInput: false,
        maxYear: parseInt(moment().format('YYYY'),10)
    }).on('apply.daterangepicker', function(ev, picker) {
        setSelected((state)=>{
            return {...state, birthDate: picker.startDate.format('MM/DD/YYYY')};
        });
    });
    // , (start, end, label) => {
    //     setSelected((state)=>{
    //         return {...state, birthDate: start.format('YYYY-MM-DD')};
    //     });
    //     // var years = moment().diff(start, 'years');
    //     // alert("You are " + years + " years old!");
    // });
      
    // date hired
    $('input[name="dateHired"]').daterangepicker({
        singleDatePicker: true,
        showDropdowns: true,
        minYear: 1980,
        autoUpdateInput: false,
        maxYear: parseInt(moment().format('YYYY'),10)
    }).on('apply.daterangepicker', function(ev, picker) {
        setSelected((state)=>{
            return {...state, dateHired: picker.startDate.format('MM/DD/YYYY')};
        });
    });

    // modal
    $('#staticBackdrop').on('hidden.bs.modal', modalClosed);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[]);

  const modalClosed = () =>{
      setSelected(formData);
      setIsUpdate(false);
  }

  const getSelected = (rowData) =>{
    
    rowData.birthDate = moment(rowData.birthDate).format('MM/DD/YYYY');
    rowData.dateHired = moment(rowData.dateHired).format('MM/DD/YYYY');
    setSelected(rowData);
    setIsUpdate(true);
    $('#staticBackdrop').modal('show');
    $('input[name="birthDate"]').data('daterangepicker').setStartDate(new Date(rowData.birthDate));
  }
  
  const handleChange = (e) => {
    const target = e.target;
    setSelected((state)=>{
        return {...state, [target.name]: target.value}
    });
  }

  const updateUser = async (e, keys = '') => {
      let data = selected;
      if(keys){
          data = {...data, ...keys};
      }
      const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/user/${selected._id}/update`, data)
      if(res.status === 200){
         tblRef.current.update('_id', data);
          (window.toastr.success(CONSTANT.SUCCESS))
          $('#staticBackdrop').modal('hide');
      }else{
          window.toastr.error(CONSTANT.ERRROR);
          console.log(res.data);
      }
  }
  const disableUser = async (e) => {
    await updateUser(e, {active: false});  
  }
  return (
    <>
        {/* data table */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Employee Lists</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                  getSelected={getSelected}
                  header={header}
                  ref={tblRef}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
  
        {/* <!-- Modal --> */}
        <Modal title="Employee Information">
            <div className="card-body row">
                {/* personal information */}
                <div className="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">Personal Information</div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputEmail1">First Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.firstName}
                    name="firstName"
                    className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Last Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.lastName}
                    name="lastName"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Middle Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.middleName}
                    name="middleName"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Birth Date</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.birthDate}
                    readOnly
                    name="birthDate"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Age</label>
                    <input type="text" 
                    value={
                        // selected.birthDate? (moment().diff(moment(selected.birthDate, "MM/DD/YYYY"), 'years', false)) : 'Invalid'
                        selected.birthDate? (moment().diff(new Date(selected.birthDate), 'years')) : 0
                    }
                    readOnly
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Civil Status</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.civilStatus}
                    name="civilStatus"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Contact Num</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.contactNum}
                    name="contactNum"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-8 col-md-8">
                    <label htmlFor="exampleInputPassword1">Address</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.address}
                    name="address"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Email</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.email}
                    name="email"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="position">Position</label>
                    <select name="position"
                    onChange={handleChange} value={selected.position}
                    className="custom-select rounded-0" id="position">
                        <option value="" disabled>Please Select Option</option>
                        {pos.map(pos=>(<option value={pos._id} key={pos._id}>{pos.position}</option>))}
                    </select>
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="department">Department</label>
                    <select name="department"
                    onChange={handleChange} value={selected.department}
                    className="custom-select rounded-0" id="department">
                        <option value="" disabled>Please Select Option</option>
                        {dept.map(dept=>(<option value={dept._id} key={dept._id}>{dept.department}</option>))}
                    </select>
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="branch">Branch</label>
                    <select name="branch"
                    onChange={handleChange} value={selected.branch}
                    className="custom-select rounded-0" id="branch">
                        <option value="" disabled>Please Select Option</option>
                        {branch.map(branch=>(<option value={branch._id} key={branch._id}>{branch.branch}</option>))}
                    </select>
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">Date Hired</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.dateHired}
                    name="dateHired"
                    readOnly
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">SSS</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.SSS}
                    name="SSS"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">HDMF</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.HDMF}
                    name="HDMF"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="exampleInputPassword1">PhilHealth</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.philHealth}
                    name="philHealth"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-4 col-md-6">
                    <label htmlFor="role">role</label>
                    <select name="role"
                    onChange={handleChange} value={selected.role}
                    className="custom-select rounded-0" id="role">
                        <option value="standard">Standard User</option>
                        <option value="sudo">Admin</option>
                    </select>
                </div>
                {/* company information */}
                <div className="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">Educational Background</div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputPassword1">Primary</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.primarySch}
                    name="primarySch"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputPassword1">Secondary</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.secondarySch}
                    name="secondarySch"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputPassword1">Tertiary</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.tertiarySch}
                    name="tertiarySch"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputPassword1">Course</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.course}
                    name="course"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>

                {/* Company Background */}
                <div className="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">Company Background</div>
                
                <div className="form-group col-sm-12 col-lg-10 col-md-10">
                    <label htmlFor="exampleInputPassword1">Company Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.company1}
                    name="company1"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-2 col-md-2">
                    <label htmlFor="exampleInputPassword1">Total years</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.year1}
                    name="year1"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-10 col-md-10">
                    <label htmlFor="exampleInputPassword1">Company Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.company2}
                    name="company2"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-2 col-md-2">
                    <label htmlFor="exampleInputPassword1">Total years</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.year2}
                    name="year2"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-10 col-md-10">
                    <label htmlFor="exampleInputPassword1">Company Name</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.company3}
                    name="company3"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <div className="form-group col-sm-12 col-lg-2 col-md-2">
                    <label htmlFor="exampleInputPassword1">Total years</label>
                    <input type="text" 
                    onChange={handleChange} value={selected.year3}
                    name="year3"
                    className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
            {/* </div> */}
            </div>
            {/* <!-- /.card-body --> */}
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" className="btn btn-danger" onClick={disableUser}>Disable</button>
                {!isUpdate? 
                (<button type="button" className="btn btn-primary">Save</button>): 
                (<>
                
                <button type="button" className="btn btn-primary" onClick={updateUser}>Update</button>
                </>)
                }
                
            </div>
        </Modal>
    </>
   );
}
 
export default ManageUser;