// Validate.js validates your values as an object
import validate from 'validate.js'

const Constraints = {
  email: {
    presence: {
      message: "Cannot be blank."
    },
    email: {
      message: 'Please enter a valid email address'
    }
  },
  firstName: {
    presence: {
      message: "Cannot be blank."
    },
    format: {
      pattern: "[a-zA-Z]+",
      flags: "i",
      message: "can only contain a-z and 0-9"
    },
    length: {
      minimum: 5,
      message: 'at least 5 characters'
    },

  },
  lastName: {
    presence: {
      message: "Cannot be blank."
    },
    format: {
      pattern: "[a-zA-Z]+",
      flags: "i",
      message: "can only contain a-z and 0-9"
    },
    length: {
      minimum: 5,
      message: 'at least 5 characters'
    },

  },
  address: {
    presence: {
      message: "Cannot be blank."
    },
    format: {
      pattern: "[a-zA-Z]+",
      flags: "i",
      message: "can only contain a-z and 0-9"
    },
    length: {
      minimum: 5,
      message: 'at least 5 characters'
    },

  },
 
  contactNum: {
    presence: {
      message: "Cannot be blank."
    },
    format: {
      pattern: new RegExp("^(09|\\+639)\\d{9}$"),
      message: "Invalid mobile number"
    },
    length: {
      minimum: 5,
      message: 'at least 5 characters'
    },

  }
}


const Validator = (field, value) => {
  // Creates an object based on the field name and field value
  // e.g. let object = {email: 'email@example.com'}
  // let object = {}
  // object[field] = value

  let constraint = Constraints[field]
  // console.log(object, constraint)
  // console.log(constraint);

  // Validate against the constraint and hold the error messages
  const result = validate({[field]: value}, { [field]: constraint })
  // const result = validate({[field]: value}, Constraints);

  console.log(result)
  // If there is an error message, return it!
  if (result && result.hasOwnProperty([field])) {
    // Return only the field error message if there are multiple
    return result[field][0]
   }

  return null
}


export default Validator;

