import { useEffect, useRef, useState } from "react";
// import Auth from "../../Auth";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
import fetch from "../../Services/fetch";

const $ =  window.$;

const PageAuthorization = () => {
  
  const tblRef = useRef();
  const header = [
    {title: 'First Name', data: 'firstName'},
    {title: 'Middle Name', data: 'middleName'},
    {title: 'Last Name', data: 'lastName'},
    {title: 'Email', data: 'email'},
    
  ];
  // const[isUpdate, setIsUpdate] = useState(false);
  const[selected, setSelected] = useState({
    _id: '',
  });
  const[pages, setPages] = useState([]);
  useEffect(()=>{
    // get user data
    fetch.get(`${process.env.REACT_APP_HOST}/api/page/user`)
    .then(res=>{
      tblRef.current.init(res.data);
    })
    .catch(er=>{
      console.log(er)
    })

    // page data
    fetch.post(`${process.env.REACT_APP_HOST}/api/page`)
    .then(res=>{
      setPages(res.data);
    })
    .catch(er=>{
      console.log(er)
    })

    // modal
    $('#staticBackdrop').on('hidden.bs.modal',(e) => {
      // setIsUpdate(false); 
   });
  },[]);

  const getSelected = async (rowData) =>{
    
    setSelected(rowData);

    const rawPages  = await pages.reduce((acc, curr) => {
      if(rowData.pageauth.length > 0){
        const checkedList = rowData.pageauth[0].pages.filter(rec => rec._idpage === curr._id);
        if(checkedList.length > 0){
          curr['value'] = checkedList[0].value;
        }else {
          curr['value'] = false;
        }
        
      }else{
        curr['value'] = false;
      }
      acc.push(curr);
      return acc;
    }, []);

    setPages(rawPages);
    // console.log(rawPages);
    $('#staticBackdrop').modal('show');
  }

  const handleChange = async (e, index, pageid) => {
    let handlePage = pages;
    handlePage[index]['value'] = !pages[index]['value'];
    setPages([...handlePage]);
    
    fetch.post(`${process.env.REACT_APP_HOST}/api/page/${selected._id}/user-role`,{
      _idpage: e.target.value,
      value: e.target.checked
    })
    .then(res=>{
      
      const selectedRow = selected;
      const page = [];
      
      pages.forEach(val => {
          page.push({_idpage: val._id, value: val.value});
      });
      
      if(selectedRow.pageauth.length < 1){
        selectedRow.pageauth.push({pages: page});
      }else{
        selectedRow.pageauth[0]['pages'] = page;
      }
      
      tblRef.current.update('_id', selectedRow);

    })
    .catch(er=>{
      console.log(er)
    })
    
  }
  return (
    <>
        {/* data table */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Employee Lists</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                  getSelected={getSelected}
                  header={header}
                  ref={tblRef}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
  
        {/* <!-- Modal --> */}
        <Modal title="User page access" size="modal-md">
            <div className="card-body">
                {pages.map((page, i) => {
                  const randomId = Math.floor(Math.random() * Date.now());
                  return (
                <div className="form-group  custom-control custom-checkbox" key={page._id}>
                    <input id={randomId} 
                    onChange={(e) => handleChange(e, i)}
                    value={page._id}
                    checked={page.value}
                    type="checkbox" className="custom-control-input" />
                    <label htmlFor={randomId} className="custom-control-label">{page.title}</label>
                </div>)})
              }
            </div>
           
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
        </Modal>
    </>
   );
}
 
export default PageAuthorization;