class Auth{
    constructor() {
        this.isAuth = false;
        this.user = '';
        this.role = '';
        this._id = '';
        this.token = '';
        this.userInfo = '';
        if(localStorage.getItem('user')) {
            const user = JSON.parse(localStorage.getItem('user'));
            this.isAuth = true;
            this._id = user._id;
            this.user = user.user;
            this.role = user.role;
            this.token = user.token;
            this.userInfo = user;
        }
    }

    login(obj , cb) {
        console.log(obj);
        localStorage.setItem('user', JSON.stringify(obj));
        this.token = obj.token || '';
        this._id = obj._id || '';
        this.user = obj.user || '';
        this.role = obj.role || '';
        this.isAuth = true;
        this.userInfo = obj;
        return cb();
        
    }

    logout(cb) {
        localStorage.clear();
        this.isAuth = false;
        return cb();
    }
}
export default new Auth();