import { useEffect } from "react";
import Auth from "../../Auth";
import AsideAdmin from "./AsideAdmin";
// import AsideContractor from "./AsideContractor";
import Aside from "./Aside";
// import AsideStandard from "./AsideStandard";
import Footer from "./Footer"
import Nav from "./Nav"
const $ = window.$;
const Layout = (props) => {
    
    useEffect(()=>{
        $(".collapse").on('show.bs.collapse', function(){
            $(this).parent().children(':first-child').addClass('collapse-open');
          });
          $(".collapse").on('hide.bs.collapse', function(){
            $(this).parent().children(':first-child').removeClass('collapse-open');
          });
    },[])
    const renderSidebar = () =>{
        switch(Auth.role){
            case 'sudo': 
                return <AsideAdmin/>

            // case 'contractor':
            //     return <AsideContractor/>
                
            // case 'site-evaluator':
            //     return <Aside/>
                
            default:
            return  <Aside/>
            
            
        }
    }
    return ( 
    <div className="wrapper">
        <Nav prop={props}/>
        {/* <!-- Main Sidebar Container --> */}
        {renderSidebar()}
        
        <div className="content-wrapper">
            <div className="content-header"></div>
            <div className="content">
                <div className="container-fluid">
                    {props.children}
                </div>
            </div>
        </div>
        <Footer/>
    </div> 
    );
}
 
export default Layout;