// import logo from './logo.svg';
// import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import ContractorEval from "./Views/ContractorEval";
import Page404 from "./Views/ErrorPages/Page404";
import Home from "./Views/Home/Home";
import Login from "./Views/Login/Login";
import Document from "./Views/Contractors/Document";
import ProtectedRoute from "./Views/ProtectedRoute";
import Register from "./Views/Register/Register";
import ManageUser from "./Views/Admin/ManageUser";
import ManageDepartment from "./Views/Admin/ManageDepartment";
import ManageBranch from "./Views/Admin/ManageBranch";
import ManagePosition from "./Views/Admin/ManagePosition";
import PageAuthorization from "./Views/Admin/PageAuthorization";
import AllSites from "./Views/Sites/AllSites";
import Page503 from "./Views/ErrorPages/Page503";
import ContractorEvalSummary from "./Views/ContractorEvalSummary";
import Messages from "./Views/Message/Message";
import SAQFiles from "./Views/Sites/SAQFiles";

function App() {
  
  return (
    <Router>
    {/* <div className="App"> */}
      <Switch>
        <ProtectedRoute exact path="/" component={Home} title="dashboard"/>
        
        {/* admin */}
        <ProtectedRoute exact  path="/manage-user" component={ManageUser} title="Manage Users" />
        <ProtectedRoute exact  path="/manage-department" component={ManageDepartment} title="Manage Department" />
        <ProtectedRoute exact  path="/manage-position" component={ManagePosition} title="Manage Position" />
        <ProtectedRoute exact  path="/manage-branch" component={ManageBranch} title="Manage Branch" />
        <ProtectedRoute exact  path="/manage-message" component={Messages} title="Messages" />
        <ProtectedRoute exact  path="/manage-sites" component={AllSites} title="Messages" />

        {/* Sites */}
        <ProtectedRoute exact  path="/saq-files" component={SAQFiles} title="SAQ FILES" />

        {/* autorizations */}
        <ProtectedRoute exact  path="/authorization-pages" component={PageAuthorization} title="Page Authorization" />

        {/* contractor */}
        <ProtectedRoute exact  path="/contractor-evaluator" component={ContractorEval} title="contractor evaluation"/>
        <ProtectedRoute exact  path="/contractor-eval-summary" component={ContractorEvalSummary} title="contractor evaluation Summary "/>
        <ProtectedRoute exact  path="/contractor-documents" component={Document} title="My Documents" />
        <ProtectedRoute exact  path="/contractor-payment" component={Page503} title="Payment History" />
        <ProtectedRoute exact  path="/contractor-progress-report" component={Page503} title="Progress Report" />
        
        {/* global */}
        <ProtectedRoute exact  path="/message" component={Messages} title="Messages" />
        <ProtectedRoute exact  path="/reports" component={Page503} title="Reports" />
        <ProtectedRoute exact  path="/all-sites" component={AllSites} title="All Sites" />
        <ProtectedRoute exact  path="/regional-team" component={Page503} title="Regional Team" />
        <ProtectedRoute exact  path="/area-supervisor-team" component={Page503} title="Regional Team" />
        

        <Route exact  path="/login" component={Login}/>
        <Route exact  path="/register" component={Register}/>
        <Route exact  path="/404" component={Page404} />
        <Route path="*" component={Page404} />
      </Switch>
    {/* </div> */}
    </Router>
  );
}

export default App;
