import Auth from "../Auth";
import fetch from "../Services/fetch";
const defaultImage = 'https://images.unsplash.com/photo-1540206395-68808572332f?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=581&q=80';
const Thumbnail = ({props}) => {
  // const randomId = Math.floor(Math.random() * Date.now());
  
  const preview = (evt, url) => {
    const output = document.getElementById(`img${props.name}`);
      let href = document.getElementById(`_newtablink${props.name}`);
      
      const objUrl = URL.createObjectURL(evt.target.files[0]);
      output.src = objUrl;
      href.href = url;
      
      Auth.userInfo.documents[evt.target.name] = url;
      localStorage.setItem('user', JSON.stringify(Auth.userInfo));
      output.onload = function() {
        URL.revokeObjectURL(output.src) // free memory
        // URL.revokeObjectURL(href.href) // free memory
      }
  }
  const handleChange = async (evt) => {
      const form = new FormData();
      
      form.append("file", evt.target.files[0]);
      form.append("name", evt.target.name);
      
      fetch.post(`${process.env.REACT_APP_HOST}/api/contractor/${Auth._id}/documents`, form, 'file')
      .then(res=>{
         preview(evt, res.data.data);
      })
      .catch(er=>{
        console.log(er)
      })
      
  }
  return (
  <div className="thumbnail">
    <label htmlFor={`ID${props.name}`} className="w-100 px-3 my-3">{props.title || ''}</label>
    <div className="docu-attachment">
        <img 
        style={{height: `${props.height || 200}px`, width: '100%', background: 'gray', objectFit: 'cover'}}
        src={props.src || defaultImage} 
        id={`img${props.name}`}
        alt="DO174"/>
        <div className="attachment-option">
          <div className="d-flex justify-content-center align-items-center">
              <label htmlFor={`ID${props.name}`}>
                <i className="fas fa-camera-retro"></i>
              </label>
              <a href={props.src} id={`_newtablink${props.name}`} target="_blank" rel="noreferrer">
                <i className="far fa-images"></i>
              </a>
          </div>
        </div>
    </div>
    <input type="file" name={props.name} onChange={handleChange} accept="image/*" id={`ID${props.name}`} className="d-none"/>
  </div>);
}
 
export default Thumbnail;