import { useEffect, useState } from "react";
import './message.css';
import Auth from "../../Auth";
import Modal from "../../Components/Modal";
import fetch from "../../Services/fetch";
const $ = window.$;
const Messages = () => {
  const[compose, setCompose] = useState({
    to: [],
    from: Auth._id,
    subject: '',
    message: '',
  });  
  
  // search for to's
  const[lastInsertedName, setLastInsertedName] = useState('');
  const[search, setSearch] = useState([]);
  const[searchList, setSearchList] = useState([]);

  const [msgList, setMsgList] = useState([
    {from: 'Alexander Pierce', subject: '4th-JARB', message: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores nemo tempore nisi autem doloremque veniam suscipit alias eveniet, provident recusandae et dolore! Ipsa voluptas voluptates animi excepturi iusto nostrum quaerat!'},
    {from: 'Alexander Pierce', subject: 'JARB', message: 'consectetur adipisicing elit. Asperiores nemo tempore nisi autem doloremque veniam suscipit alias eveniet, provident recusandae et dolore! Ipsa voluptas voluptates animi excepturi iusto nostrum quaerat!'},
    {from: 'Alexander Pierce', subject: '4', message: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores nemo tempore nisi autem doloremque veniam suscipit alias eveniet, provident recusandae et dolore! Ipsa voluptas voluptates animi excepturi iusto nostrum quaerat!'},
    {from: 'Alexander Pierce', subject: 'Theq quick brown fox jump over the lazy dog', message: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores nemo tempore nisi autem doloremque veniam suscipit alias eveniet, provident recusandae et dolore! Ipsa voluptas voluptates animi excepturi iusto nostrum quaerat!'},

  ]);

  useEffect(()=>{
    $('#compose-textarea').summernote({
      height: 300,
      placeholder: 'Compose a message..',
    });

    fetch.get(`${process.env.REACT_APP_HOST}/api/user/${Auth._id}`)
    .then(res => {
        console.log(res);
    })
    .catch(er=>{
      console.log(er);
    })
  },[]);

  const handleChange = async (e) => {
    const target = e.target;
    setCompose({...compose, [target.name]: target.value});
  }

  // search users
  const handleSearch = async (e) => {
    const value = (e.target.value).split(",");
    setSearch(value);
    const keyword = (value[value.length-1]).trim();
    const {data} = await fetch.post(`${process.env.REACT_APP_HOST}/api/user/search`, {
      key: keyword
    });
    setSearchList(data);
  }
  
  // submit message
  const submit = () => {
    const markup = $('#compose-textarea').summernote('code');
    console.log(markup);
  }

  // selected row
  const selected = (msg) => {
    setCompose({...compose, message: msg.message, subject: msg.subject, to: msg.from});
    $('#compose-textarea').summernote('code', msg.message);
    $('#staticBackdrop').modal('show');
  }

  const appendTo = (val) => {
    const fullName = (val.firstName+' '+val.lastName).trim();

    
    const arrayTos = compose.to;
    arrayTos.push({
      _iduser: val._id,
      name: fullName,
      email: val.email,
    });
    setCompose({ ...compose, to: arrayTos });

    // console.log(search);
    // return;
    let tempSearch = search;
    
    search ? tempSearch[tempSearch.length - 1] = fullName : tempSearch.push(fullName);
    tempSearch.push("");
    // tempSearch.toString();
    
    // return;
    setSearch(tempSearch);
    setSearchList([]);
  }

  return ( <div className="row">
    <div className="card card-primary card-outline col-lg-12">
            <div className="card-header">
              <h3 className="card-title">Inbox</h3>

              <div className="card-tools">
                <div className="input-group input-group-sm">
                  <input type="text" className="form-control" placeholder="Search Message" />
                  <div className="input-group-append">
                    <div className="btn btn-primary">
                      <i className="fas fa-search"></i>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- /.card-tools --> */}
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body p-0">
              <div className="mailbox-controls">
                {/* <!-- Check all button --> */}
                <button type="button" className="btn btn-default btn-sm checkbox-toggle">
                  <i className="far fa-square"></i>
                </button>
                <div className="btn-group">
                  <button type="button" className="btn btn-default btn-sm">
                    <i className="far fa-trash-alt"></i>
                  </button>
                  <button type="button" className="btn btn-default btn-sm" data-toggle="modal" data-target="#staticBackdrop">
                    <i className="far fa-sticky-note"></i>
                  </button>
                  {/* <button type="button" className="btn btn-default btn-sm">
                    <i className="fas fa-share"></i>
                  </button> */}
                </div>
                {/* <!-- /.btn-group --> */}
                <button type="button" className="btn btn-default btn-sm">
                  <i className="fas fa-sync-alt"></i>
                </button>
                <div className="float-right">
                  1-50/200
                  <div className="btn-group">
                    <button type="button" className="btn btn-default btn-sm">
                      <i className="fas fa-chevron-left"></i>
                    </button>
                    <button type="button" className="btn btn-default btn-sm">
                      <i className="fas fa-chevron-right"></i>
                    </button>
                  </div>
                  {/* <!-- /.btn-group --> */}
                </div>
                {/* <!-- /.float-right --> */}
              </div>
              <div className="table-responsive mailbox-messages">
                <table className="table table-hover table-striped">
                  <tbody>
                  {
                    msgList.map((msg, key)=>{
                        return (
                        <tr key={key}>
                          <td>
                            <div className="icheck-primary">
                              <input type="checkbox" name="checkbox" value="" id={"check"+key} />
                              <label htmlFor={"check"+key}></label>
                            </div>
                          </td>
                          {/* <td className="mailbox-star"><a href="#"><i className="fas fa-star text-warning"></i></a></td> */}
                          <td className="mailbox-name"><a href="#/" onClick={()=>selected(msg)}>{msg.from}</a></td>
                          <td className="mailbox-subject"><b>{msg.subject}</b> - {(msg.subject + msg.message).slice(msg.subject.length, 50)+'...'}
                          </td>
                          {/* <td className="mailbox-attachment"><i className="fas fa-paperclip"></i></td> */}
                          <td className="mailbox-date">5 mins ago</td>
                        </tr>)
                    })
                  }
                  
                </tbody>
              </table>
            </div>
          </div>
        </div>
    <Modal title="New Message">
        <div className="card-body">
              <div className="form-group position-relative">
                <input 
                value={search}
                onChange={handleSearch}
                name="to"
                className="form-control" placeholder="To:" />
                <div className={searchList.length && search? "message-search-list position-absolute": "d-none"}>
                    {searchList.map(res => {
                     return (
                      <button type="button" 
                      onClick={()=>appendTo(res)}
                      className="list-group-item list-group-item-action px-4" key={res._id}>
                         {res.firstName +' '+ res.lastName}
                      </button>
                     ); 
                    })}
                </div>
              </div>
              <div className="form-group">
                <input 
                value={compose.subject}
                onChange={handleChange}
                name="subject"
                className="form-control" placeholder="Subject:" />
              </div>
              <div className="form-group">
                  <div id="compose-textarea" 
                  // value={compose.message} 
                  // onChange={handleChange}
                  // name="message"
                  // className="form-control"
                  >
                    {compose.message}
                  </div>
              </div>
                  
              
              {/* <div className="form-group">
                <div className="btn btn-default btn-file">
                  <i className="fas fa-paperclip"></i> Attachment
                  <input type="file" name="attachment" />
                </div>
                <p className="help-block">Max. 32MB</p>
              </div> */}
            {/* </div> */}
        </div>
        <div className="modal-footer card-footer text-right">
          <button type="button" 
          onClick={submit}
          className="btn btn-primary" data-dismiss="modal">Send
            <i className="fab fa-telegram-plane pl-2"></i>
          </button>
      </div>
    </Modal>
  </div> );
}
 
export default Messages;